  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  height: 100%;
}

.nav-links li a {
  color: rgba(0, 0, 0, 0.6);
  font-weight: bold;
  text-decoration: none;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  display: block;
  cursor: pointer;
  position: relative;
}

.nav-links li a:hover {
  color: black;
}

