    padding-left: 0px;
    padding-right: 0px;
    justify-content: space-between;
  }
  .nav-links {
    display: block;
    list-style: none;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    width: 100%;
    margin-top: 15px;
    margin: 15px auto;
  }
  .nav-links li {
    margin: 0 auto;
    text-align: center;
    height: 40px;
